@import url('https://fonts.googleapis.com/css2?family=Cormorant:wght@400;500;600&family=Source+Sans+Pro:wght@300;400&display=swap');
*{
  font-family: 'Cormorant', serif;
  margin:0;
  box-sizing: content-box;

}


.container { 
  max-width: 1920px;
  margin: 0 auto;
}

.introduction-area { 
  height: 100vh;
  overflow: hidden;

  .header {

    position: absolute;
    z-index: 999;

    display: flex;
    justify-content: flex-start;

    margin-left: 45px;
    margin-top:20px;

    color:white;
    .header__logo {
      margin-right:180px;
      .header__img{ 
        width: 60px;
        height: 70px;
      }
    }
    .header__call-section{
      display: inherit;
      width:230px;
      display: flex;
      justify-content: flex-start;
      align-items: center;
      .burger-menu {
        display: none;
        img {
          width:30px;
          height:30px;
        }
      }
      .call-btn{
        margin-right: 24px;
      }
      .call-info{
        width:200px;
        
        display: flex;
        align-items: center;
        justify-content: space-between;

        font-size: 16px;

        transition: display 3s;

        .call-number{
          color: #A68E52;
          margin-bottom: 8px;
          font-family: 'Source Sans Pro', sans-serif;
          display: block;
        }
        .call-text{
          padding-bottom: 4px;
          border-bottom: 1px solid #A68E52;
          font-family: 'Source Sans Pro', sans-serif;
        }
      }
      .call-btn, .current-language{
        width: 30px;
        height:30px;

        border: 1px solid #757575;
        border-radius: 50%;

        background-color: transparent;
      }
      .call-btn {
        display: inline;
      }
      .current-language{
        font-size: 10px;
        color: white;
  
      }

    }

  }
  .contacts {
    width:100%;
  
    display: flex;
    justify-content: flex-start;
  
    position: relative;
    z-index: 999;
  
    bottom:5%;
  
    .contacts__address {
      width: 100%;
      display: flex;
  
      color: #DDDDDD;
      opacity: 0.7;
  
      text-transform: uppercase;
  
      padding-left: 100px;
      .sub_contacts {
        margin-right: 20px;
      }
    }
    .sub_logo{
      width:100%;
  
      display: flex;
      justify-content: flex-end;
  
      padding-right: 40px;
    }
  }
}
@media (max-width:736px)  {
  .introduction-area {
  .contacts {
    bottom: 15%;

    font-size:12px;

    background-color: rgba(0,0,0, 0.1);
    padding: 0 30px;
    display: flex;
    justify-content: space-between;
    .contacts__address {

      display: flex;
      align-items: center;

      padding-left: 0px;
      margin-right: 35%;


      width: auto;
      .sub_contacts {
        display: flex;
        align-items: center;
        margin-right:10px;
        img {
          width: 8px;
          height: 8px;
          margin-right:5px;
        }
      }
    }
    .sub_logo {
      width:auto;
      padding-right:5px;
      img {
        height:40px;
      }
    }
  }
}
}
